import React from 'react';

import './Multi.css';

import Single from '../Single/Single';


const Multi = (props) => 
{
    const ReturnData = props.Data.map((item, index)=>
    {
        return (
            <Single key={"single_"+index} Data={item}/>
        );   
    });

    return(
        <div>
            <div className="result_status">
                <span>Znalazłem dla Ciebie {props.Data.length} wyników.</span>
            </div>
            {ReturnData}
        </div>
    )
}

export default Multi;