import React from 'react';

import './Search.css'

function getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}


const Search = (props) => 
{
    const setState = () =>
    {
        let text = document.getElementById('user-query').value;
        props.State.set(text);
    }

    const title = ["Czego nie wiesz?", "Raz.. dwa.. trzy.. Pytasz Ty!", "Zapytaj o coś", "Wpisz a się dowiesz", "Znajdę odpowiedź na każde pytanie", 'Podaj jakieś słowo', 'A może Asymetryczna aminohydroksylacja Sharplessa?', 'Lubię jak ktoś mnie pyta', "Nadal czekam..."];
    let index = getRandomInt(0, title.length-1);

    return (
        <div className="search_container">
            <div className="title">
                <span>{title[index]}</span>
            </div>
            <div className="search">
                <input id="user-query" type="search" placeholder="Wprowadź pojęcie..."></input>
            </div>
            <div className="submit">
                <button type="button" onClick={()=>{setState();}}>Szukaj</button>
            </div>
        </div>
    );
}

export default Search;