import React from 'react';

import './Single.css';

import PDF from '../img/pdf.png';


const download = (title) =>
{
    window.open('https://pl.wikipedia.org/api/rest_v1/page/pdf/'+title,'_blank');
}

const Single = (props) =>
{
    console.log(props.Data);
    return (
        <div className="single_container">
            <div className="variable">
                <span>{props.Data.title}</span>
            </div>
            <div className="single_title">Definicja</div>
            <div className="definition" dangerouslySetInnerHTML={{__html:props.Data.extract_html}}></div>
            <div className="single_title">Źródło</div>
            <div className="link">
                <a href={props.Data.content_urls.desktop.page}>{props.Data.content_urls.desktop.page}</a>
            </div>
            <div className="single_title">Operacje</div>
            <div className="operation">
                <div className="pdf">
                    <figure onClick={()=>download(props.Data.title)}>
                        <img alt="pdf" width={100} height={100} title="Wygeneruj plik PDF całego artykułu" src={PDF}></img>
                        <figcaption>Generuj plik PDF</figcaption>
                    </figure>
                </div>
            </div>
        </div>
    )
}

export default Single;