import React from 'react';
import ReactDOM from 'react-dom';

import './Result.css';
import Loading from './img/loading.gif';

import Single from './Single/Single';
import Multi from './Multi/Multi';
import None from './None/None';
const getData = async (obj) =>
{

    try
    {
        let res = await fetch('https://pl.wikipedia.org/api/rest_v1/page/summary/'+obj.State.get+'?redirect=true');
        let data = await res.json();
        if(data.type === 'standard')
        {
            ReactDOM.render(
                <Single Data={data}/>,
                document.getElementById('content')
            );
        }
        else if (data.type === "disambiguation")
        {
            let res = await fetch('https://pl.wikipedia.org/api/rest_v1/page/html/'+obj.State.get);
            let data = await res.text();
            let doc = new DOMParser().parseFromString(data, "text/xml");
            let list = doc.getElementsByTagName('ul');
            let tab = [];
            for(let i of list)
            {
                for(let j of i.children)
                {
                    let title = j.children[0].attributes.title
                    if(title){
                        tab.push(j.children[0].attributes.title.value);
                    }
                }
            }
            let dict = [];
            for(let item of tab)
            {
                try
                {
                    let r = await fetch('https://pl.wikipedia.org/api/rest_v1/page/summary/'+item);
                    let rj = await r.json();
                    if(rj.content_urls)
                    {
                        dict.push(rj);
                    }
                }
                catch(e)
                {   
                    console.log(e);
                }
            }
            console.log(dict);
            ReactDOM.render(
                <Multi Data = {dict}/>,
                document.getElementById('content')
            );
            //document.getElementById('info').innerHTML = list[0].innerHTML;
        }
        else
        {
            ReactDOM.render(
                <None/>,
                document.getElementById('content')
            );
        }
    }
    catch(e)
    {
        console.error(e);
        ReactDOM.render(
            <None/>,
            document.getElementById('content')
        );
    }
}

const Result = (props) => 
{
    getData(props);

    return (
        <div id = "content">
            <div style={{textAlign:"center"}}>
                <img alt="loading" width={400} height={300} src={Loading}></img>
            </div>
        </div>
    );
}

export default Result;